import React from "react";
import {motion} from 'framer-motion'

export default function Recipe({ ingredients, image, title, titleLength }) {
  return (
    <div className="recipe_item">
      <h2 className="recipe_item--title">{title}</h2>
      <div className="recipe_item--content">
        <img className="recipe_item--image" src={image} />
        <div className="recipe_item--ingredients">
          <h3
            className={
              19 > titleLength ? `recipe_item--ingredients-title` : "hide"
            }
          >
            Ingredients
          </h3>
          <ul className="ingredients-container">
            {ingredients.map((each) => {
              return (
                <li key={Math.floor(Math.random() * 100000)}>{each.text}</li>
              );
            })}
          </ul>
        </div>
      </div>
    </div>
  );
}
