import React, { useEffect, useState, useRef } from "react";
import ReactDOM from "react-dom";
import "../scss/main.scss";
import Recipe from "./Recipe";
import axios from "axios";
import {motion} from 'framer-motion'

function App() {
  const [recipes, setRecipes] = useState([]);
  const [search, setSearch] = useState("chicken");

  let input = useRef("");

  useEffect(() => {
    axios
      .get(
        `https://api.edamam.com/api/recipes/v2?type=public&q=${search}&app_id=5448d2c6&app_key=a40037aa1c2c305fa9c8bf662ef43007`
      )
      .then((response) => {
        setRecipes(response.data.hits);
      });
  }, [search]);

  const handleSubmit = (e) => {
    e.preventDefault();
    setSearch(input.current.value);
    input.current.value = "";
  };

  return (
    <div className="container">
      <form onSubmit={handleSubmit}>
        <h1 className="logo">Recipes</h1>

        <input
          className="form-input"
          ref={input}
          type="text"
          placeholder="search recipes"
          onChange={(e) => {
            input.current.value = e.target.value;
          }}
        />
        <button type="search">Search</button>
      </form>

      <motion.div 
      animate={{opacity:1}}
      initial ={{opacity:0}}
      exit ={{opacity:0}}
      transition={{duration:1}}
      className="recipe-container">
        {recipes.map((each, index) => {
          return (
            <Recipe
              key={index}
              ingredients={each.recipe.ingredients}
              title={each.recipe.label}
              titleLength={each.recipe.label.length}
              image={each.recipe.image}
            />
          );
        })}
      </motion.div>
    </div>
  );
}

ReactDOM.render(<App />, document.getElementById("root"));
